  --tw-gradient-from:#8b5cf6;
  --tw-gradient-stops:var(--tw-gradient-from), var(--tw-gradient-to, rgba(139, 92, 246, 0));
  --tw-gradient-stops:var(--tw-gradient-from), #6366f1, var(--tw-gradient-to, rgba(99, 102, 241, 0));
  --tw-gradient-to:#6366f1
}

.editFilm .ant-switch:focus{
  box-shadow:none
}

.editFilm .ant-input-number, .editFilm .ant-picker{
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity))
}

.editFilm .ant-input-number:focus-within, .editFilm .ant-picker:focus-within{
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity))
}

.editFilm .ant-input-number:hover, .editFilm .ant-picker:hover{
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity))
}

.editFilm .ant-input-number, .editFilm .ant-picker{
  --tw-shadow:0 0 #0000;
  box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

.showtimes .ant-picker{
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity))
}

.showtimes .ant-picker:focus-within{
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity))
}

.showtimes .ant-picker:hover{
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity))
}

.showtimes .ant-picker{
  --tw-shadow:0 0 #0000;
  box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

@-webkit-keyframes modal-video{
  from{
    opacity:0
  }

  to{
    opacity:1
  }
}

@keyframes modal-video{
  from{
    opacity:0
  }

  to{
    opacity:1
  }
}

@-webkit-keyframes modal-video-inner{
  from{
    -webkit-transform:translate(0, 100px);
    transform:translate(0, 100px)
  }

  to{
    -webkit-transform:translate(0, 0);
    transform:translate(0, 0)
  }
}

@keyframes modal-video-inner{
  from{
    -webkit-transform:translate(0, 100px);
    transform:translate(0, 100px)
  }

  to{
    -webkit-transform:translate(0, 0);
    transform:translate(0, 0)
  }
}

.modal-video{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-color:rgba(0,0,0,0.5);
  z-index:1000000;
  cursor:pointer;
  opacity:1;
  -webkit-animation-timing-function:ease-out;
  animation-timing-function:ease-out;
  -webkit-animation-duration:.3s;
  animation-duration:.3s;
  -webkit-animation-name:modal-video;
  animation-name:modal-video;
  transition:opacity .3s ease-out
}

.modal-video-effect-exit{
  opacity:0
}

.modal-video-effect-exit .modal-video-movie-wrap{
  -webkit-transform:translate(0, 100px);
  transform:translate(0, 100px)
}

.modal-video-body{
  max-width:960px;
  width:100%;
  height:100%;
  margin:0 auto;
  padding:0 10px;
  display:flex;
  justify-content:center;
  box-sizing:border-box
}

.modal-video-inner{
  display:flex;
  justify-content:center;
  align-items:center;
  width:100%;
  height:100%
}

@media (orientation: landscape){
  .modal-video-inner{
    padding:10px 60px;
    box-sizing:border-box
  }
}

.modal-video-movie-wrap{
  width:100%;
  height:0;
  position:relative;
  padding-bottom:56.25%;
  background-color:#333;
  -webkit-animation-timing-function:ease-out;
  animation-timing-function:ease-out;
  -webkit-animation-duration:.3s;
  animation-duration:.3s;
  -webkit-animation-name:modal-video-inner;
  animation-name:modal-video-inner;
  -webkit-transform:translate(0, 0);
  transform:translate(0, 0);
  transition:-webkit-transform .3s ease-out;
  transition:transform .3s ease-out;
  transition:transform .3s ease-out, -webkit-transform .3s ease-out
}

.modal-video-movie-wrap iframe{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%
}

.modal-video-close-btn{
  position:absolute;
  z-index:2;
  top:-45px;
  right:0px;
  display:inline-block;
  width:35px;
  height:35px;
  overflow:hidden;
  border:none;
  background:transparent
}

@media (orientation: landscape){
  .modal-video-close-btn{
    top:0;
    right:-45px
  }
}

.modal-video-close-btn:before{
  -webkit-transform:rotate(45deg);
  transform:rotate(45deg)
}

.modal-video-close-btn:after{
  -webkit-transform:rotate(-45deg);
  transform:rotate(-45deg)
}

.modal-video-close-btn:before,.modal-video-close-btn:after{
  content:'';
  position:absolute;
  height:2px;
  width:100%;
  top:50%;
  left:0;
  margin-top:-1px;
  background:#fff;
  border-radius:5px;
  margin-top:-6px
}

